import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

class Box extends React.Component {
  constructor(props){
    super(props);
    var w = 10,
        h = 10;
    this.state = {
      w: w,
      h: h,
      arr: new Array(w*h).fill(''),
      val: '',
      win: false
    }
    this.btnclick = this.btnclick.bind(this);
  }
  //
  //
  //i,  intvaldir
  geTctnuArr(i, intval, dir){
    var b = [],
        arr = this.state.arr,//
        w = this.state.w,//
        h = this.state.h;//
    var val = arr[i];
    //
    //lr
    //tb
    //false
    //i, 
    function isgb(i){
      var lr = (i + 1)%w,//01
          tb = Math.ceil((i + 1)/w);//1
      if((lr === 0) || (lr === 1)){//
        return 'lr';
      }else if((tb === 1) || (tb === h)){//
        return 'tb';
      }else{//
        return false;
      }
    }
    //
    function istrue(i){
      if(dir === 'lr'){
        return !(isgb(i) === 'lr');//
      }else if(dir === 'tb'){
        return !(isgb(i) === 'tb');//
      }else{
        return !(isgb(i));//
      }
    }
    //
    while(123){//
      //
      if(arr[i - intval] === val && istrue(i)){
        i -= intval;
      }else{
        break;
      }
    }
    while(123){//
      b[b.length] = i;
      //
      if((arr[i + intval] === val) && istrue(i)){
        i += intval;
      }else{
        break;
      }
    }
    return b;
  }
  //
  reset(){
    var w = this.state.w,//
        h = this.state.h;//
    this.setState({
      w: w,
      h: h,
      arr: new Array(w*h).fill(''),
      val: '',
      win: false
    })
  }
  btnclick(e){
    var arr = this.state.arr,
        index = parseInt(e.target.getAttribute('data-index'), 10),
        val = this.state.val;
    if(arr[index] || this.state.win){
      return false;
    };
    val === 'X' ? val = 'O' : val = 'X';
    arr[index] = val;
    this.setState({
      arr: arr,
      val: val
    });
    this.isWin(index);
  }
  isWin(index){
    var arr = this.state.arr,//
        w = this.state.w,//
        val = arr[index];//
    if(
      (this.geTctnuArr(index, 1, 'lr').length >= 5)||//
      (this.geTctnuArr(index, w, 'tb').length >= 5)||//
      (this.geTctnuArr(index, (w - 1)).length >= 5)||//
      (this.geTctnuArr(index, (w + 1)).length >= 5)//
      ){
        this.setState({win: (val + '')});
      }
  }
  render() {
    var arr = this.state.arr,
        elearr = arr.map((txt, index) => (<div className="grid-btn" data-index={index} onClick={this.btnclick} key={index}>{this.state.arr[index]}</div>));
    return (
      <div>
        <div className="player-ele">{this.state.win || ((this.state.val === 'X' ? 'O' : 'X') + '')}</div>
        <div className="player-ele">
          <button className="reset-btn" onClick={() => {this.reset()}}></button>
        </div>
        <div className="grid-box" style={{width: ((this.state.w*30 - this.state.w) + 'px')}}>{elearr}</div>
      </div>
    );
  }
}
ReactDOM.render(
  <Box/>,
  document.getElementById('root')
);



